<template>
    <div>
        질문 제목 : <input class="input" type="text" v-model.trim="qnaTitle"><br><br>          
        질문 내용 : <textarea type="text" v-model.trim="qnaContent"></textarea><br>          
        <button v-on:click="addTodo">추 가</button>
    </div>  
</template>

<script>
import axios from "../js/axios-common.js";


export default {
    data : function() {
        return { 
            qnaTitle : "",
            qnaContent: "",
            qnaUserid: "",
        }
    },
    methods : {
        addTodo : function() {
            axios
            .get('/api/qna/id')
            .then((response)=>{
                this.qnaUserid = response.data;
                this.$store.dispatch('addQnA', {qnaTitle : this.qnaTitle, 
                                                qnaContent: this.qnaContent, 
                                                qnaUserid: this.qnaUserid});
                this.qnaTitle = "";
                this.qnaContent = "";
                this.qnaUserid = "";
                this.$router.push('/list');
            })
            .catch(exp => alert("추가처리에 실패하였습니다." + exp));
            
            

            
            //this.$store.dispatch('allQnA');
            //location.href ="/happyhouse/";
        }
    }
}
</script>

<style>
div{
    text-align: center;
}

</style>