  actions: {
    //all***axios 요청 후 결과가 오면 mutation(store.commit)에 지시해서 state 변경 요청
    allQnA(store){
        axios
        .get("/api/qna")
        .then(response => { //직접 데이터 처리 안됨
            store.commit('allQnA', { qnalist: response.data });
            console.log('haha',response.data); })//서버에서 받은 데이터로 todolist 셋팅해줌            
        .catch(exp => alert("allQnA처리에 실패하였습니다." + exp));
    },
    addQnA(store, payload){
      
      axios.post('/api/qna',payload)
      .then((response) => {
          console.log(response.data);
          console.log('입력 처리하였습니다.', response.data);
          store.dispatch('allQnA');//다시 액션에 새데이터 FETCH 요청
      })
      .catch(exp => alert('입력 처리에 실패하였습니다.' + exp)); 
    },
    showQnA(store, payload){
      axios.get('/api/qna/' +payload)
      .then((response) => {
          console.log('조회 처리하였습니다.', response.data);
          store.commit('showQnA', { qna:response.data });
      })
      .catch(exp => alert('조회 처리에 실패하였습니다.' + exp)); 
    },
    deleteQnA(store, payload){
      axios.delete('/api/qna/' + payload)
      .then(() => {
          //store.dispatch('allQnA');
          console.log('삭제 처리하였습니다.');
          store.dispatch('allQnA');
      })
      .catch(exp => alert('삭제 처리에 실패하였습니다.' + exp));
    },
    updateQnA(store, payload){
      axios.put('/api/qna/', payload)
      .then(() => {
          console.log('수정 처리하였습니다.');
          store.dispatch('allQnA');
        })
      .catch(exp => alert('수정 처리에 실패하였습니다.' + exp));
    },
    
  },
  mutations: {
    allQnA(state, payload){
      state.qnalist = payload.qnalist;
      console.log('kkk',state.qnalist);
    },
    showQnA(state,payload){
      console.log(payload);             
      state.qna = payload.qna;  
    }
  },

